import { PDFDocument, StandardFonts, rgb, PageSizes } from 'pdf-lib';
import fontkit from '@pdf-lib/fontkit';
import { eachDayOfInterval, isSaturday, format } from 'date-fns';
import SignaturePad from 'signature_pad';
import * as download from 'downloadjs'
import { logEvent, analytics } from './analytics';

const DOMAIN = 'https://briut-ok.web.app/';
const TEXT_SIZE = 8;
let templatePdfArrayBuffer = null;



Object.defineProperty(Array.prototype, 'chunk', {
    value: function(chunkSize) {
        var R = [];
        for (var i = 0; i < this.length; i += chunkSize)
            R.push(this.slice(i, i + chunkSize));
        return R;
    }
});

const setHebrewSupportedFont = async (pdfDoc) => {
    const fontUrl = DOMAIN + 'VarelaRound-Regular.ttf'
    const fontUrlBytes = await fetch(fontUrl).then(res => res.arrayBuffer())
    return  await pdfDoc.embedFont(fontUrlBytes);
}

const insertTemplateToDoc = async (pdfDoc) => {
    if (!templatePdfArrayBuffer) {
        const templatePdfUrl = DOMAIN + 'template.pdf'
        templatePdfArrayBuffer = await fetch(templatePdfUrl).then((res) => res.arrayBuffer())
    }
    const templatePdfDoc = await PDFDocument.load(templatePdfArrayBuffer)
    return await pdfDoc.copyPages(templatePdfDoc, [0])
}


const generatePage = async (pdfDoc) => {
    const [page] = await insertTemplateToDoc(pdfDoc);
    pdfDoc.addPage(page);
    return page
}

const getLocations = initialLocation => {
    const OFFSET_X = 282;
    const OFFSET_Y = 215;
    return  [
        {
            x: initialLocation.x,
            y: initialLocation.y
        },
        {
            x: initialLocation.x + OFFSET_X,
            y: initialLocation.y
        },
        {
            x: initialLocation.x,
            y: initialLocation.y - OFFSET_Y
        },
        {
            x: initialLocation.x + OFFSET_X,
            y: initialLocation.y - OFFSET_Y
        },
        {
            x: initialLocation.x,
            y: initialLocation.y - (2 * OFFSET_Y)
        },
        {
            x: initialLocation.x + OFFSET_X,
            y: initialLocation.y - (2 * OFFSET_Y)
        },
    ];
}

const getPageItemLocations = (page, text, initialCoords, customFont) => {
    const initialLocation = {
        x: page.getWidth() - initialCoords.x - customFont.widthOfTextAtSize(text, TEXT_SIZE),
        y: initialCoords.y
    };
    const allLocations = getLocations(initialLocation);
    return allLocations.map(location => ({
        text,
        customFont,
        x: location.x,
        y: location.y
    }))
}
const getPageItemLocationsForDates = (page, dates, initialCoords, customFont) => {
    const initialLocation = {
        x: page.getWidth() - initialCoords.x - customFont.widthOfTextAtSize(dates[0], TEXT_SIZE),
        y: initialCoords.y
    };
    const allLocations = getLocations(initialLocation);
    return allLocations.map((location, i) => ({
        text: dates[i],
        customFont,
        x: location.x,
        y: location.y
    }))
}
const getPageItemLocationsForSignature = (page, pngString, initialCoords) => {
    const initialLocation = {
        x: page.getWidth() - initialCoords.x, // - customFont.widthOfTextAtSize(dates[0], TEXT_SIZE),
        y: initialCoords.y
    };
    const allLocations = getLocations(initialLocation);
    return allLocations.map((location, i) => ({
        pngString,
        x: location.x,
        y: location.y
    }))
}

const drawItems = (page, pageObjectsWithLocations) => {
    pageObjectsWithLocations.forEach(pageObject => {
        const {text, customFont, x, y} = pageObject;
        if (!text) {
            return;
        }
        page.drawText(text, { x, y, size: 8, font: customFont})
    })
}

const drawSignatures = async (page, pdfDoc, pageObjectsWithLocations) => {
    for (const pageObject of pageObjectsWithLocations) {
        const {x, y, pngString} = pageObject;
        if (!x) {
            return;
        }
        const pngImage = await pdfDoc.embedPng(pngString);
        const pngImageDims = pngImage.scale(0.1)
        page.drawImage(pngImage, { x, y, width: pngImageDims.width, height: pngImageDims.height })
    }
}

const createPageWithData = async (pdfDoc, pageObjects, customFont) => {
    const page = await generatePage(pdfDoc);

    const date1Locations = getPageItemLocationsForDates(page, pageObjects.dates, {x: 475, y:672}, customFont);
    const studentNameLocations = getPageItemLocations(page, pageObjects.studentName, {x: 368, y: 659}, customFont);
    const studentIdLocations = getPageItemLocations(page, pageObjects.studentId, {x: 380, y: 648}, customFont);
    const studentParentNameLocations = getPageItemLocations(page, pageObjects.parentName, {x: 365, y: 588}, customFont);
    const studentParentIdLocations = getPageItemLocations(page, pageObjects.parentId, {x: 365, y: 577}, customFont);
    const date2Locations = getPageItemLocationsForDates(page, pageObjects.dates, {x: 355, y:567}, customFont);
    const sigLocations = getPageItemLocationsForSignature(page, pageObjects.sig, {x: 410, y: 543});

    drawItems(page, date1Locations);
    drawItems(page, studentNameLocations);
    drawItems(page, studentIdLocations);
    drawItems(page, studentParentNameLocations);
    drawItems(page, studentParentIdLocations);
    drawItems(page, date2Locations);
    drawSignatures(page, pdfDoc, sigLocations);
}

const getEndDate = () => {
    const END_MONTH = 7;
    const testDate = new Date();
    testDate.setMonth(END_MONTH);
    const endYear = Date.now() < testDate.getTime() ? (new Date()).getFullYear() : (new Date()).getFullYear() + 1;
    return endYear;
}

const generatePdf = async () => {
    const pdfDoc = await PDFDocument.create();
    pdfDoc.registerFontkit(fontkit);
    const customFont = await setHebrewSupportedFont(pdfDoc);
    const studentName = document.getElementById('studentName').value;
    const studentId = document.getElementById('studentId').value;
    const parentName = document.getElementById('parentName').value;
    const parentId = document.getElementById('parentId').value;
    const sig = signaturePad.toDataURL();

    const daysTillEndOfYear = eachDayOfInterval(
        { start: new Date(), end: new Date(getEndDate(), 7, 1) }
    )
    const daysTillEndOfYearNoSaturdays = daysTillEndOfYear.filter(date => !isSaturday(date))
    const dates = daysTillEndOfYearNoSaturdays.map(date => format(date, "d/M/yy"))
    const chunksOfDates = dates.chunk(6);

    for (const chunk of chunksOfDates) {
        const pageObjects = {
            dates: chunk,
            studentName,
            studentId,
            parentName,
            parentId,
            sig
        }
        await createPageWithData(pdfDoc, pageObjects, customFont);
    }




    const pdfBytes = await pdfDoc.save()

    // Trigger the browser to download the PDF document
    download(pdfBytes, "pdf-lib_creation_example.pdf", "application/pdf");
}

const setLoadingButton = async setLoading => {
    let elm = document.getElementById('exec');
    if (setLoading) {
        elm.classList.add('is-loading');
    } else {
        elm.classList.remove('is-loading');
    }
}
const clearFields = () => {
    document.getElementById('studentName').value = '';
    document.getElementById('studentId').value = '';
    document.getElementById('parentName').value = '';
    document.getElementById('parentId').value = '';
    signaturePad.clear()
}

const onButtonClick = async () => {
    try {
        setLoadingButton(true);
        await generatePdf();
        clearFields();
        goToFinishedView();
        logEvent(analytics, 'generate_form_success');
    } catch (e) {
        console.error(e)
    } finally {
        setLoadingButton(false);
    }
}

const goToFinishedView = () => {
    document.getElementById('execView').classList.add('hide');
    document.getElementById('finishedView').classList.remove('hide');
}

const goBackToExecView = () => {
    document.getElementById('execView').classList.remove('hide');
    document.getElementById('finishedView').classList.add('hide');
    signaturePad.clear()
}

const canvas = document.querySelector("canvas");
const signaturePad = new SignaturePad(canvas, {});
const clearSig = () => { signaturePad.clear() }

document.getElementById('exec').onclick = onButtonClick;
document.getElementById('back').onclick = goBackToExecView;
document.getElementById('clearSig').onclick = clearSig;


function resizeCanvas() {
    var ratio =  Math.max(window.devicePixelRatio || 1, 1);
    canvas.width = canvas.offsetWidth * ratio;
    canvas.height = canvas.offsetHeight * ratio;
    canvas.getContext("2d").scale(ratio, ratio);
    signaturePad.clear(); // otherwise isEmpty() might return incorrect value
}

window.addEventListener("resize", resizeCanvas);
resizeCanvas();

